var tha;
import {mapMutations} from 'vuex';
export default {
	onLoad(){
		tha = this;
	},
	data() {
		return {
			phoneno:'',
			password:''
		};
	},
	methods: {
	    bindLogin() {
			if (this.phoneno.length != 11) {
			     uni.showToast({
			        icon: 'none',
			        title: '手机号不正确'
			    });
			    return;
			}
	        if (this.password.length < 6) {
	            uni.showToast({
	                icon: 'none',
	                title: '密码不正确'
	            });
	            return;
	        }
			
			this.$api.login({
				mobile:this.phoneno,
				password:this.password
			}).then(res => {
				uni.showToast({
				    icon: 'none',
				    title: '登录成功'
				});
				this.$api.getInfo().then(res => {
					uni.setStorageSync('userMsg', JSON.stringify(res.data));
					this.$store.dispatch('getuserinfo')
					if (res.data && res.data.unread_group_count && res.data.unread_group_count > 0) {
						uni.setTabBarBadge({
						  index: 1,
						  text: res.data.unread_group_count + ''
						})	
					}
					if (res.data.status != 10) {
						uni.switchTab({
							url: '/pages/user/user'
						});
					} else {
						uni.switchTab({
							url: '/pages/home/home'
						});
					}
				})
			}).catch(err => {
			})
			
	    }
	}
}
